<template>
  <div id="dashboard">
    <h3>Dashboard</h3>

    <input v-model="text" class="text" type="text">
    <span>{{text}}</span>
    <button @click="fetchData">Add</button>
    <ul class="list">
      <li class="item">Foo</li>
      <li v-for="item in list" v-bind:key="item.id">
        {{item.name}}
      </li>
    </ul>
    <span v-html="html"></span>
    <span v-text="html"></span>

    <div>
      <h2>Disablaed</h2>
      <span v-if="isDiss">isDiss</span>
      <button @click="toggle">toggle isDiss</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'dashboard',
    data () {
      return {
        list: [],
        text: '',
        html: '<p>tay take</p>',
        isDiss: false
      }
    },
    created () {
      this.list.push({
        id: 0,
        name: 'Valerka'
      })
    },
    beforeRouteEnter (to, from, next) {
      console.log({to, from, next})
      next(vm => vm.list.push({
        id: 1,
        name: 'Karton'
      }))
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        if (!this.text) return;
        this.list.push({
          id: this.list.length,
          name: this.text
        });
        this.text = ''
      },
      toggle () {
        this.isDiss = !this.isDiss;
      }
    }
  }
</script>

<style scoped>
.text {
  color: red;
}
</style>



